import { Component, OnInit } from "@angular/core";
import { PagesService } from "./pages.service";


@Component({
    templateUrl: './pages.template.html',
    styleUrls: ['./pages.styles.scss']
})
export class PagesComponent implements OnInit {

    pages;

    constructor(
        private pageService: PagesService
    ){}

    ngOnInit() {
    	console.log("big oof here");
        this.pageService.getPages().then((data) => {
            this.pages = data || [];
        });

    }

    editPage(pageName) {

    }

    deletePage(id) {
        this.pages.forEach(page => {
            if(page[1] == id){
                let index = this.pages.indexOf(page);
                this.pages.splice(index,1);
            }
        });
        console.log(id);
    }


    replaceOrder(page) {
        var id, position, currentPostition;

        [id,position] = page.split('|');
        this.pages.forEach(page => {
            if(page[1] == id){ currentPostition = this.pages.indexOf(page); }
        });
        [this.pages[position], this.pages[currentPostition]] = [this.pages[currentPostition], this.pages[position]];
    }
}
