import { Component, OnInit } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { Router } from "@angular/router";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AuthenticationService } from "src/app/core/services";
import { PagesService } from "../pages.service";


@Component({ 
    templateUrl: './pages.create.template.html',
    styleUrls: ['./pages.create.styles.scss']
})
export class PagesCreateComponent implements OnInit {
    		
	public pageForm: FormGroup;
	public submitted: boolean;
	loaded:boolean;

    constructor(
		private pageService: PagesService,
		private formBuilder: FormBuilder,
		private toast: ToastrService,
	 	private router: Router, 
    ){}

    
    ngOnInit(){
        this.loaded = true;
        this.pageForm = this.formBuilder.group({
			url: ['', [Validators.required]],  
        });
    }


	create() {
		this.submitted = true;
		if (!this.pageForm.valid) { 
		 	//eerorservice
			this.toast.error("oops neco se nezdarilo..zkontrolujte pole");
		 	return;
		}

	
		let data = {
            instance: window['instance_id'], 
            url: this.pageForm.get("url").value,
            page_type: ""
        } 

		this.pageService.createPage(data).then((data) => {
				console.log(data);
				//TODO get id and set perms 
				this.router.navigate(['redactor/pages'])
			}, (error) => {
				//todo errorhandler
				this.submitted = false;
				console.log("error:" + error);
			}	
		);
	}


}
