import { Routes } from "@angular/router";
import { PagesComponent } from "./pages.component";
import { PagesCreateComponent } from "./create/pages.create.component";
import { PagesEditComponent } from "./edit/pages.edit.component";

/**
 *  
 * 
 * 
 */


// Actual moudule routes
export const pages_routes: Routes = [ 
    {
        path: 'pages',
        loadChildren: 'src/app/common/modules/pages/pages.module#PagesModule'
    }
] 

// Required for lazy-loading
export const routes: Routes = [ 
    {
        path: '',
        component: PagesComponent
    },
    {
        path: 'create',
        component: PagesCreateComponent
    },
    {
        path: 'edit/:pageId',
        component: PagesEditComponent
    },
]
