import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { AuthenticationService, EnvironmentService } from 'src/app/core/services';
import { CookiesService } from 'src/app/core/services/cookies/cookies.service';

@Injectable()
export class PagesService {

    constructor(
        public http: HttpClient,
        public envService: EnvironmentService,
        public cookies: CookiesService,
        public authentication: AuthenticationService
    ){}

    public getPages(): any{
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl') +'/'+ window['instance_id'] + '/pages').subscribe(resolve, reject);
        });  
    }

    public getPageInfo(gridId): any{
        return new Promise((resolve,reject) => {
            this.http.get(this.envService.read('apiUrl') +'/grid/' + gridId).subscribe(resolve, reject);
        });  
    }

    public deletePage(gridId): any{
        let authinfo = this.authentication._authinfo.get();
        const options = {
            headers: new HttpHeaders({'Content-Type': 'application/json'}),
            body: authinfo
        };
        return new Promise((resolve,reject) => {
            this.http.delete(this.envService.read('apiUrl') +'/grid/' + gridId,options).subscribe(resolve, reject);
        });  
    }

    public editPage(gridId,data ){
        let authinfo = this.authentication._authinfo.get();
        return new Promise((resolve,reject) => {
            this.http.patch(this.envService.read('apiUrl') +'/grid/' + gridId,{auth_info:authinfo,grid:data}).subscribe(resolve, reject);
        });  
    }

    public createPage(data){
        let authinfo = this.authentication._authinfo.get();
        let grid = {
            auth_info: authinfo,
            grid: data
        }
        return new Promise((resolve,reject) => {
            this.http.post(this.envService.read('apiUrl') +'/grid/new',grid).subscribe(resolve, reject);
        });  
    }

}


