import { Component, OnInit } from "@angular/core";
import { ToastrService } from "ngx-toastr";
import { Router, ActivatedRoute } from "@angular/router";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AuthenticationService } from "src/app/core/services";
import { PagesService } from "../pages.service";


@Component({ 
    templateUrl: './pages.edit.template.html',
    styleUrls: ['./pages.edit.styles.scss']
})
export class PagesEditComponent implements OnInit {
    		
	public pageForm: FormGroup;
    public submitted: boolean;
    pageId;
	loaded:boolean;

    constructor(
		private pageService: PagesService,
		private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private toast: ToastrService,
	 	private router: Router, 
    ){}

    
    ngOnInit(){
        this.loaded = false;
        this.route.paramMap.subscribe(params => { this.pageId = params.get("pageId") });

        this.pageService.getPageInfo(this.pageId).then(data => {
            this.pageForm = this.formBuilder.group({
                url: [data.url, [Validators.required]],  
            });
            this.loaded = true;
        })
        
    }


	create() {
		this.submitted = true;
		if (!this.pageForm.valid) { 
		 	//eerorservice
			this.toast.error("oops neco se nezdarilo..zkontrolujte pole");
		 	return;
		}

	
		let data = {
            instance: window['instance_id'], 
            url: this.pageForm.get("url").value,
            page_type: ""
        } 

		this.pageService.editPage(this.pageId,data).then((data) => {
				console.log(data);
				//TODO get id and set perms 
				this.router.navigate(['redactor/pages'])
			}, (error) => {
				//todo errorhandler
				this.submitted = false;
				console.log("error:" + error);
			}	
		);
	}


}
